import { useEffect, useState } from "react";

interface Props {
  defaultLoading?: boolean;
  root?: Element;
  delay?: number;
}
interface Return {
  start: () => Promise<void>;
  loading: boolean;
}

export default function useImgsLoaded({
  defaultLoading = true,
  root = document.querySelector("#root") as Element,
  delay,
}: Props): Return {
  const [loading, setLoading] = useState(defaultLoading);

  const waitImgsLoaded = async (root: Element) => {
    const imgs =
      root instanceof HTMLImageElement ? [root] : root.querySelectorAll("img");

    return await Promise.all(
      Array.from(imgs).map(
        (img) =>
          new Promise((resolve) => {
            if (img.complete) {
              resolve(img);
              return;
            }
            img.addEventListener("load", () => resolve(img));
          })
      )
    );
  };

  const start = async () => {
    await waitImgsLoaded(root);
    setLoading(false);
  };

  useEffect(() => {
    async function wait() {
      await waitImgsLoaded(root);
    }
    if (!delay) {
      wait().then(() => {
        setLoading(false);
      });
    } else {
      setTimeout(() => {
        wait().then(() => {
          setLoading(false);
        });
      }, delay);
    }
  }, [delay, root]);

  return {
    loading,
    start,
  };
}
