import { Outlet, useNavigate, useLocation } from "react-router-dom";

import Logo from "@/assets/logo.png";

const routes = [
  { name: "热门模板", path: "/tpls" },
  { name: "内推广场", path: "/interpolation" },
  { name: "背景中心", path: "/bgs" },
];

export default function Header() {
  const navigate = useNavigate();
  const location = useLocation();

  const handleClick = (href: string) => navigate(href);

  return (
    <div>
      <header className="h-20  w-full bg-white flex justify-between items-center content-center px-10 fixed z-10 shadow-xl">
        <h3 className="w-2/12">
          <img
            src={Logo}
            alt=""
            className="h-16 cursor-pointer"
            onClick={() => handleClick("/")}
          />
        </h3>

        <div className="w-full h-10 leading-10 text-lg font-medium ">
          {routes.map((item) => (
            <span
              className="mx-4 inline-block cursor-pointer text-gray-600"
              key={item.path}
              style={{
                height: "3.9rem",
                borderBottom:
                  location.pathname === item.path ? "3px solid #1890ff" : "",
                color: location.pathname === item.path ? "#1890ff" : "",
              }}
              onClick={() => handleClick(item.path)}
            >
              {item.name}
            </span>
          ))}
        </div>

        <i
          title="icon-github"
          className="iconfont icon-github rounded-full h-10 w-10 pt-1.5 pl-3 cursor-pointer"
          onClick={() =>
            window.open("https://github.com/MasonEast/MasonEast.github.io")
          }
        />
      </header>
      <Outlet />
      <div className="p-10"></div>
    </div>
  );
}
